<script>
import { getProject } from "../../api/project";
import { messages, imgUrl } from "../../utils";
import { collect, cancelCollect } from "../../api/public";
// 引入基本模板
import echarts from "echarts/lib/echarts";
// 再引入你需要使用的图表类型
import "echarts/lib/chart/bar";
import "echarts/lib/chart/line";
// 引入提示框和title组件，图例
import "echarts/lib/component/title";
import "echarts/lib/component/tooltip";

import {
  Button,
  Icon,
  Table,
  Timeline,
  TimelineItem,
  Affix,
  Tag,
  Poptip
} from "iview";
export default {
  components: {
    [Button.name]: Button,
    [Table.name]: Table,
    [Timeline.name]: Timeline,
    [Affix.name]: Affix,
    [TimelineItem.name]: TimelineItem,
    [Poptip.name]: Poptip,
    [Tag.name]: Tag,
    [Icon.name]: Icon
  },
  data() {
    return {
      //默认地址路径
      imgUrl: imgUrl,
      //默认头像
      logoUrl: require("../../assets/img/user.jpg"),
      //
      show: false,
      active: 0,
      data: {
        logo: "",
        isColler: "未收藏",
        rates: []
      },
      menusList: ["基本信息", "项目信息", "团队成员", "项目进度"],
      columns1: [
        {
          title: "姓名",
          key: "name"
        },
        {
          title: "职位",
          key: "position"
        },
        {
          title: "介绍",
          key: "introduce"
        }
      ]
    };
  },
  methods: {
    clickOpen(url) {
      window.open(imgUrl + url);
    },
    //跳转至相关内容
    clickActive(_index) {
      this.active = _index;
      var scrollNub =
        document.getElementsByClassName("block1")[_index].offsetTop - 40;
      document.documentElement.scrollTop = scrollNub;
    },
    //添加收藏
    clickCollect() {
      collect({ code: this.data.code, type: 1 }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success("收藏成功");
          this.data.isColler = "已收藏";
        }
      });
    },
    //取消收藏
    clickCancelCollect() {
      cancelCollect({ code: this.data.code, type: 1 }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success("取消收藏");
          this.data.isColler = "未收藏";
        }
      });
    },
    //获取项目详情
    getData() {
      getProject({ id: this.$route.params.id }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.data = res.data.data;
          this.data.label = this.data.label.split(",");
          this.data.createTime = this.data.createTime.split(" ")[0];
          this.data.address = JSON.parse(this.data.address).name;
          // 基于准备好的dom，初始化echarts实例
          echarts.init(document.getElementById("main1")).setOption({
            animationEasing: "bounceOut",
            title: {
              text: "年度融资金额 单位：万元"
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            tooltip: {},
            xAxis: {
              data: this.data.statistical.xAxis
            },
            yAxis: {},
            series: [
              {
                name: "每月融资",
                type: "bar",
                data: this.data.statistical.series.moneys,
                itemStyle: {
                  normal: {
                    color: "#2d8df0", //设置柱子颜色
                    label: {
                      show: true, //柱子上显示值
                      position: "top", //值在柱子上方显示
                      textStyle: {
                        color: "#000000" //值得颜色
                      }
                    }
                  }
                }
              }
            ]
          });
          echarts.init(document.getElementById("main2")).setOption({
            animationEasing: "bounceOut",
            title: {
              text: "每月融资笔数 单位：笔"
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            xAxis: {
              type: "category",
              data: this.data.statistical.xAxis
            },
            yAxis: {
              type: "value"
            },
            series: [
              {
                data: this.data.statistical.series.nums,
                type: "line"
              }
            ]
          });
        }
      });
    },
    //监听滚动
    getScroll() {
      let active = document.documentElement.scrollTop;
      let active3 = document.getElementsByClassName("block1")[3].offsetTop - 70;
      let active2 = document.getElementsByClassName("block1")[2].offsetTop - 70;
      let active1 = document.getElementsByClassName("block1")[1].offsetTop - 70;
      let active0 = document.getElementsByClassName("block1")[0].offsetTop - 70;
      if (active < active1) {
        this.active = 0;
      } else if (active > active1 && active < active2) {
        this.active = 1;
      } else if (active > active2 && active < active3) {
        this.active = 2;
      } else if (active > active3) {
        this.active = 3;
      }
    }
  },
  mounted() {
    this.getData();
    window.addEventListener("scroll", this.getScroll);
  },
  watch: {
    $route(to, from) {
      this.getData();
    }
  },
  destroyed() {
    window.removeEventListener("scroll", this.getScroll);
  }
};
</script>
